.preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: #537db4;
    color: white;
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
}
@-webkit-keyframes pulse {
    0% {
        background-color: rgba(42, 65, 92, 0.5);
        color: rgba(255, 255, 255, 0.5);
    }
    50% {
        background-color: #537db4;
        color: white;
    }
    100% {
        background-color: rgba(42, 65, 92, 0.5);
        color: rgba(255, 255, 255, 0.5);
    }
}
@keyframes pulse {
    0% {
        background-color: rgba(42, 65, 92, 0.5);
        color: rgba(255, 255, 255, 0.5);
    }
    50% {
        background-color: #537db4;
        color: white;
    }
    100% {
        background-color: rgba(42, 65, 92, 0.5);
        color: rgba(255, 255, 255, 0.5);
    }
}



