}

.sidebar {
    width: 35%;
    overflow: auto;
    height: 90vh;
}

.sidebar ul {
    min-height: calc(100% - 34px);
    padding-left: 20px;
}

.content-viewer-container {
    width: 70%;
    overflow: auto;
    height: 90vh;
}

.content-viewer {
    min-height: 90%;
}

.readme-viewer {
    padding: 0 50px;
}

.navbar{
    border-radius: 0;
}

.css-79elbk {
    overflow: auto;
}

pre {
    white-space: pre-wrap;
    word-wrap: break-word;
    text-align: justify;
    /*padding-left: 30px;*/
    padding: 0;
    margin: 0;
    height: 90vh
}

.row {
    margin-right: 10px;
    margin-left: 10px;
}

.hljs {
    min-height: 89vh;
    font-size: 16px;
    padding: 30px 30px 0 30px;
}

.form-container {
    width: 40%;
    margin: auto;
    background-color: #F1F1F1;
    height: 90vh
}

legend {
    margin: 20px auto;
    color: unset;
    border: unset;
    width: unset;
}

.home-page-navigation {
    display: flex;
    float: right;
}

.home-page-navigation a {
    font-size: 1.25em;
}

.navbar-brand {
    font-size: 1em;
}

.form-horizontal {
    background-color: #F1F1F1;
    height: 90vh;
    color: #3e3e3e;
}

.form-horizontal .form-group {
    margin: 0 0 40px 0;
}

.error {
    color: tomato;
}

.success {
    color: blue;
}

/* width */
::-webkit-scrollbar {
    width: 8px;
    height: 3px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #3C3F41;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.search-box {
    background-color: #404448;
    color: #eef9ff;
}

#search .navbar-inverse {
    background-color: unset;
    border: unset;
}

.home-page-navigation .navbar-brand {
    padding-left: 45px;
}

.container {
    width: 90%;
}



@media only screen and (max-width: 640px) {
    .tree-viewer-container {
        display: unset;
    }

    .sidebar {
        width: 100%;
        overflow: auto;
        height: 40vh;
    }

    .content-viewer-container {
        width: 100%;
    }

    .form-horizontal .form-group {
        margin-right: 0;
        margin-left: 0;
    }

    .form-container {
        width: 90%;

    }

    .home-page-navigation {
        display: unset;
        float: unset;
    }

    .home-page-navigation .navbar-brand {
        float: unset;
        height: 40px;
    }

    .home-page-navigation .navbar-nav {
        margin: unset;
    }

    .home-page-navigation .navbar-toggle:hover {
        background-color: unset;
    }

    .home-page-navigation .navbar-collapse {
        border: none;
    }

    .home-page-navigation a {
        display: block;
        text-align: right;
        font-size: 1.15em;
    }

    .row {
        margin: 0;
    }
}
#search {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
}

#search input[type="search"] {
    position: absolute;
    top: 50%;
    width: 100%;
    color: rgb(255, 255, 255);
    background: rgba(0, 0, 0, 0);
    font-size: 100px;
    font-weight: 300;
    text-align: center;
    border: 0;
    margin: -100px auto 0;
    padding-left: 30px;
    padding-right: 30px;
    outline: none;
}

#search .btn-primary {
    position: absolute;
    width: 30%;
    top: 50%;
    left: 37%;
    margin-top: 61px;
    margin-left: -45px;
}

#search .btn-secondary {
    position: absolute;
    top: 50%;
    left: 65%;
    margin-top: 61px;

}

@media all and (max-width: 980px) {
    #search input[type="search"] {
        font-size: 75px;

    }
}

@media all and (max-width: 720px) {
    #search input[type="search"] {
        font-size: 35px;

    }
}


